import { EeMovimientoDialogComponent } from './../ee-movimiento-dialog/ee-movimiento-dialog.component';
import { InlineMessageComponent } from './../../../app/layout-pages/login/login.component';
import { Subscription } from 'rxjs/Subscription';
import { Component, ElementRef, ViewChild, Input, ComponentFactory, ChangeDetectorRef, ViewContainerRef, ComponentFactoryResolver, ComponentRef, ViewChildren, EventEmitter, Output } from '@angular/core';
import { DataSource } from '@angular/cdk/collections';
import { MatSort, MatDialog } from '@angular/material';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import { Router } from "@angular/router";

export interface ColumnDefs {
    key: string;
    title: string;
}

export interface ChildTable {
    hideHeader: boolean;
    trackBy: string;
    dataSet: Array<Object>;
    columnDefs: Array<ColumnDefs>;
    sourceProperty: string;
    hideFilter: boolean;
    hasDetail?: boolean;
    detailProperty?: string;
}
@Component({
    selector: 'ee-table',
    template: require('./ee-table.component.html'),
})
export class EeTableComponent {
    @Input() header;
    @Input() columnDefs: Array<ColumnDefs>;
    @Input() source;
    @Input() sourceChanges;
    @Input() hideFilter;
    @Input() hideHeader;
    @Input() isChild;
    @Input() subChild;
    @Input() subChildSelectOne;
    @Input() hasDetail;
    @Input() selectOne;
    @Input() selectOneEdit;
    @Input() selectRowEdit;
    @Input() selectRowDelete;
    @Input() selectCobro;
    @Input() detailProperty;
    @Input() chequeStyle;
    @Input() trackBy;
    @Input() childTable: ChildTable;
    @Output() selectedOneEmit = new EventEmitter();
    @Output() selectedOneEditEmit = new EventEmitter();
    @Output() selectedOneDeleteEmit = new EventEmitter();
    @Output() selectedCobroEmit = new EventEmitter();
    displayedColumns: Array<any> = [];
    exampleDatabase;
    expandedRow: number;
    selectedId: number;
    defaultIcon: boolean = true;
    type: DynamicClass;
    dataSource: ExampleDataSource | null;
    @ViewChild(MatSort) sort: MatSort;
    @ViewChild('filter') filter: ElementRef;
    @ViewChildren('cdkrow', { read: ViewContainerRef }) containers;
    constructor(private changeDetector: ChangeDetectorRef,
        private resolver: ComponentFactoryResolver,
        private dialog: MatDialog,
        private router: Router) { }
    ngOnInit() {
        console.log(this.source);
        this.type = {}
        this.exampleDatabase = new ExampleDatabase(this.source);
        if (this.childTable) {
            this.trackBy = this.childTable.trackBy;
            this.displayedColumns.push("arrow");
        }

        this.columnDefs.forEach(element => {
            this.displayedColumns.push(element.key);
            Object.defineProperty(this.type, element.key, {});
        });

        if (this.hasDetail) {
            this.displayedColumns.push("detail");
        }
        if (this.subChildSelectOne) {
            this.displayedColumns.push("subChildSelectOne");
        }
        if (this.selectOne) {
            this.displayedColumns.push("selectOne");
        }
        if (this.selectOneEdit) {
            this.displayedColumns.push("selectOneEdit");
        }
        if (this.selectRowEdit) {
            this.displayedColumns.push("selectRowEdit");
        }
        if (this.selectRowDelete) {
            this.displayedColumns.push("selectRowDelete");
        }
        if (this.selectCobro) {
            this.displayedColumns.push("selectCobro");
        }
        this.dataSource = new ExampleDataSource(this.exampleDatabase, this.sort);
        Observable.fromEvent(this.filter.nativeElement, 'keyup')
            .debounceTime(150)
            .distinctUntilChanged()
            .subscribe(() => {
                if (!this.dataSource) { return; }
                this.dataSource.filter = this.filter.nativeElement.value;
            });
        if (this.sourceChanges) {
            this.sourceChanges.subscribe((value) => {
                this.exampleDatabase = null;
                this.exampleDatabase = new ExampleDatabase(value);
                this.dataSource = new ExampleDataSource(this.exampleDatabase, this.sort);
            });
        }
        this.changeDetector.detectChanges();
    }

    goToDetail(row) {
        console.log(row[this.detailProperty]);
    }

    selectedOne(row) {
        this.selectedOneEmit.emit(row);
    }

    selectedOneEdit(row) {
        this.selectedOneEditEmit.emit(row);
    }

    selectedOneDelete(row) {
        this.selectedOneDeleteEmit.emit(row);
    }

    selectedCobro(row, from) {
        let options = {
            row: row,
            from: from
        }
        this.selectedCobroEmit.emit(options);
    }

    subChildSelectedOne(row) {
        console.log("row", row);
        let dialogRef = this.dialog.open(EeMovimientoDialogComponent, {
            hasBackdrop: true,
            data: row
        });
    }

    expandRow(indexE: number) {

        let indice;
        this.source.forEach((element, index) => {
            if (element[this.trackBy] === indexE) {
                indice = index;
            }
        });
        if (this.expandedRow != null) {
            // clear old message
            this.containers.toArray()[this.expandedRow].clear();
        }
        this.defaultIcon = !this.defaultIcon;
        if (this.expandedRow === indice) {
            this.expandedRow = null;
            this.selectedId = null;
        } else {
            const container = this.containers.toArray()[indice];
            const factory: ComponentFactory<EeTableComponent> = this.resolver.resolveComponentFactory(EeTableComponent);
            const messageComponent = container.createComponent(factory);

            messageComponent.instance.columnDefs = this.childTable.columnDefs;
            if (!this.subChild) {
                messageComponent.instance.source = this.childTable.dataSet[indice][this.childTable.sourceProperty];
            } else {
                messageComponent.instance.source = this.source[indice]['movimientos'];
                messageComponent.instance.subChildSelectOne = true;
            }
            messageComponent.instance.hideFilter = this.childTable.hideFilter;
            messageComponent.instance.hideHeader = this.childTable.hideHeader;

            messageComponent.instance.isChild = true;
            messageComponent.instance.subChild = true;
            if (!this.subChild) {
                messageComponent.instance.childTable = {
                    hideHeader: false,
                    trackBy: 'movimientoID',
                    columnDefs: [
                        { key: 'movimientoID', title: 'ID' },
                        { key: 'fechaMovimiento', title: 'Fecha' },
                        { key: 'monto', title: 'Monto' }
                    ],
                    hasDetail: true,
                    dataSet: this.source[indice]['movimientos'],
                    detailProperty: 'movimientoID',
                    sourceProperty: 'movimientos',
                    hideFilter: true
                }
            }
            this.expandedRow = indice;
            this.selectedId = indexE;
        }
    }
}

export class DynamicClass {

}


export class ExampleDatabase {

    dataChange: BehaviorSubject<DynamicClass[]> = new BehaviorSubject<DynamicClass[]>([]);
    get data(): DynamicClass[] { return this.dataChange.value; }

    constructor(source) {
        source.forEach(element => {
            const copiedData = this.data.slice();
            copiedData.push(element);
            this.dataChange.next(copiedData);
        });
    }

}

/**
 * Data source to provide what data should be rendered in the table. Note that the data source
 * can retrieve its data in any way. In this case, the data source is provided a reference
 * to a common data base, ExampleDatabase. It is not the data source's responsibility to manage
 * the underlying data. Instead, it only needs to take the data and send the table exactly what
 * should be rendered.
 */
export class ExampleDataSource extends DataSource<any> {
    _filterChange = new BehaviorSubject('');
    get filter(): string { return this._filterChange.value; }
    set filter(filter: string) { this._filterChange.next(filter); }

    constructor(private _exampleDatabase: ExampleDatabase, private _sort: MatSort) {
        super();
    }

    /** Connect function called by the table to retrieve one stream containing the data to render. */
    connect(): Observable<DynamicClass[]> {
        const displayDataChanges = [
            this._exampleDatabase.dataChange,
            this._filterChange,
            this._sort.sortChange
        ];

        return Observable.merge(...displayDataChanges).map(() => {
            return this._exampleDatabase.data.slice().filter((item: DynamicClass) => {
                let searchStr;
                Object.keys(item).forEach(element => {
                    searchStr += (item[element]);
                });
                searchStr = searchStr.toLowerCase();
                return searchStr.indexOf(this.filter.toLowerCase()) != -1;
            });
        }).map(() => {
            return this.getSortedData();
        });
    }

    /** Returns a sorted copy of the database data. */
    getSortedData(): DynamicClass[] {
        const data = this._exampleDatabase.data.slice();
        if (!this._sort.active || this._sort.direction == '') { return data; }

        return data.sort((a, b) => {
            let propertyA: number | string = '';
            let propertyB: number | string = '';

            //switch (this._sort.active) {
            //case 'id': [propertyA, propertyB] = [a['id'], b['id']]; break;
            // case 'userName': [propertyA, propertyB] = [a.name, b.name]; break;
            // case 'progress': [propertyA, propertyB] = [a.progress, b.progress]; break;
            // case 'color': [propertyA, propertyB] = [a.color, b.color]; break;
            //}
            console.log("sort by", this._sort.active);
            [propertyA, propertyB] = [a[this._sort.active], b[this._sort.active]];
            let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
            let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

            return (valueA < valueB ? -1 : 1) * (this._sort.direction == 'asc' ? 1 : -1);
        });
    }


    disconnect() { }
}


