import { DataStorageProvider } from './../../providers/data-storage-provider';
import { EeMovimientoDialogComponent } from './../../components/common/ee-movimiento-dialog/ee-movimiento-dialog.component';
import { EeTableComponent } from './../../components/common/ee-table/ee-table.component';
import { EeChequesPDialogComponent } from './../../components/common/ee-cheques-p-dialog/ee-cheques-p-dialog.component';
import { UIMaterialsModule } from './../../components/uimaterials/uimaterials.module';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { EeResidenciaDialogComponent } from './../../components/common/ee-residencia-dialog/ee-residencia-dialog.component';
import { EELoading } from './../../providers/ee-loading-provider';
import { EeLoadingDialogComponent } from './../../components/common/ee-loading-dialog/ee-loading-dialog.component';
import { AccessGuard } from './../../providers/access-guard-provider';
import { NgModule } from '@angular/core';
import { HotkeyModule } from 'angular2-hotkeys';
import { LayoutRoutingModule } from './layout-routing.module';


@NgModule({
  imports: [
    LayoutRoutingModule,
    FormsModule,
    CommonModule,
    HotkeyModule.forRoot(),
    UIMaterialsModule
  ],
  declarations: [
    EeLoadingDialogComponent,
    EeResidenciaDialogComponent,
    EeMovimientoDialogComponent,
    EeChequesPDialogComponent
  ],
  entryComponents: [
    EeLoadingDialogComponent,
    EeResidenciaDialogComponent,
    EeChequesPDialogComponent,
    EeTableComponent,
    EeMovimientoDialogComponent
  ],
  providers: [
    AccessGuard,
    EELoading,
    DataStorageProvider
  ]
})

export class LayoutModule { }



