import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { Component, Input, OnInit } from '@angular/core';
import { APPCONFIG } from '../../config';
import { Global } from "../../../variables/Global";

@Component({
  selector: 'my-app-sidenav',
  styles: [],
  template: require('./sidenav.component.html')
})

export class AppSidenavComponent implements OnInit {
  AppConfig;
  private residencias;
  constructor(private eeloading: EELoading, private dataStg: DataStorageProvider) { }
  ngOnInit() {
    this.AppConfig = APPCONFIG;
  }

  changeResidencia() {
    let _residenciasStg = this.dataStg.getValue("residencias");
    this.residencias = _residenciasStg ? JSON.parse(_residenciasStg as string) : null;
    if (this.residencias && this.residencias.length > 1)
      this.eeloading.openResidenciaDialog(this.residencias);
  }
  toggleCollapsedNav() {
    this.AppConfig.navCollapsed = !this.AppConfig.navCollapsed;
  }
}



