import { Router } from '@angular/router';
import { DataStorageProvider } from './data-storage-provider';
import { Global } from './../variables/Global';
import { Observable } from 'rxjs/Rx';
import { Injectable } from '@angular/core';
import { HttpEvent, HttpInterceptor, HttpHandler, HttpRequest } from '@angular/common/http';
import { HttpErrorResponse } from "@angular/common/http";
import { ResponseOptions } from "@angular/http";
import { HttpResponse } from "@angular/common/http";

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
    constructor(private dataStg: DataStorageProvider, private router: Router) { }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        // Get the auth header from the service.
        // Clone the request to add the new header.
        let access_token = this.dataStg.getValue('access_token');
        if (access_token) {
            const authReq = req.clone({ headers: req.headers.set('Authorization', 'Bearer ' + access_token) });
            return next.handle(authReq)
                .catch((err: any) => {
                    return this.handleError(err);
                });
        } else {
            return next.handle(req)
                .catch((err: any) => {
                    return this.handleError(err);
                });
        }
    }

    handleError(err) {
        console.log(err);
        if (err instanceof HttpErrorResponse) {
            console.log(err);
            if (err.status === 0) {
                return Observable.throw(new HttpResponse({
                    body: { description: 'Server caido' },
                    status: 0
                }));
            }
            if (err.status === 401) {
                this.router.navigate(['login']);
                return Observable.throw(new HttpResponse({
                    body: { description: 'Sesion ha expirado' },
                    status: 401
                }));
            }
            if (err.status === 403) {
                console.info('err.error =', err.error, ';');
                return Observable.throw(err);
            }
            if (err.status === 409) {
                return Observable.throw(new HttpResponse({
                    body: { description: JSON.parse(err.error).description },
                    status: 409
                }));
            }
            return Observable.throw(err);
        }
    }
}


