import { Component, Renderer, ElementRef } from '@angular/core';

@Component({
    selector: 'ee-loading-dialog',
    template: require('./ee-loading-dialog.component.html')
})
export class EeLoadingDialogComponent {
    constructor(
        private renderer: Renderer,
        private elementRef: ElementRef) {

    }
    ngAfterViewInit() {
        // .cdk-global-overlay-wrapper 
        let elements = document.querySelectorAll('.cdk-global-overlay-wrapper')
        if (elements.length !== 0) {
            this.renderer.setElementClass(elements[0], 'custom-loading', true);
        }
    }
}



