import { DataStorageProvider } from './data-storage-provider';
import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class MovimientosProvider {

    constructor(private http: HttpClient, private dataStg: DataStorageProvider) { }

    getMovimientosFlujoFondos(residencia) {
        return this.http.get(environment.url + 'movimientos/flujofondos?residencia=' + residencia)
    }

    GetResumenMes(periodo, residencia) {
        return this.http.get(environment.url + 'movimientos/resumenMes/' + periodo + '?residencia=' + residencia)
    }

    getMovimientosFacturacionMensual(residencia) {
        return this.http.get(environment.url + 'movimientos/facturacionMensual?residencia=' + residencia)
    }

    getPersonalesByRole(role, residencia) {
        return this.http.get(environment.url + 'personal/role/' + role + '?residencia=' + residencia)
    }

    GetResumenCuentaByPersonal(username, residencia) {
        return this.http.get(environment.url + 'movimientos/cuenta/' + username + '?residencia=' + residencia)
    }

    GetResumenCuentaByPersonalId(userid, residencia) {
        return this.http.get(environment.url + 'movimientos/cuentaid/' + userid + '?residencia=' + residencia)
    }

    GetResumenCuentaByResidencia(residencia) {
        return this.http.get(environment.url + 'movimientos/cuentares/' + residencia)
    }

    GetComposicionCaja(residencia) {
        return this.http.get(environment.url + 'movimientos/composicion/' + residencia)
    }

    GetBancoDiario(residencia) {
        return this.http.get(environment.url + 'movimientos/bancodiario/' + residencia)
    }

    getCobrosPendientes(status, residencia) {
        return this.http.get(environment.url + 'movimientos/status/' + status + '?residencia=' + residencia)
    }

    getMovimientosByMedio(medio, residencia, tipo) {
        return this.http.get(environment.url + 'movimientos/medio/' + medio + '/' + tipo + '?residencia=' + residencia)
    }

    getCobrosData(residencia) {
        return this.http.get(environment.url + 'movimientos/cobros?residencia=' + residencia)
    }

    postMovimiento(movimientoForm) {
        movimientoForm.userName = this.dataStg.getValue('username');
        return this.http.post(environment.url + 'movimientos', movimientoForm)
    }

    procesarCobro(cobroID) {
        return this.http.put(environment.url + 'movimientos/cobros/procesar/' + cobroID, {})
    }

    getResidentesByResidencia(residencia) {
        return this.http.get(environment.url + 'residente/residencia/' + residencia)
    }

    getResidenteCuentaByResidenteId(residente, residencia) {
        return this.http.get(environment.url + 'movimientos/residenteCuenta/' + residente + '?residencia=' + residencia)
    }

}



