import { EeResidenciaDialogComponent } from './../components/common/ee-residencia-dialog/ee-residencia-dialog.component';
import { EeLoadingDialogComponent } from './../components/common/ee-loading-dialog/ee-loading-dialog.component';
import { MatDialog } from '@angular/material';
import { Injectable } from '@angular/core';
import { Global } from "../variables/Global";

@Injectable()
export class EELoading {
    constructor(private dialog: MatDialog) { }

    open() {
        this.dialog.open(EeLoadingDialogComponent, {
            hasBackdrop: false
        });
    }

    close() {
        this.dialog.closeAll();
    }

    openResidenciaDialog(_residencias) {
        this.dialog.open(EeResidenciaDialogComponent, {
            data: { residencias: _residencias },
            hasBackdrop: true
        });
    }
}


