import { DataStorageProvider } from './../providers/data-storage-provider';
import { EEToast } from './../providers/ee-toast-provider';
import { AuthenticationProvider } from './../providers/authentication.provider';
import { LayoutService } from './layout/layout.service';
import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router, NavigationEnd } from '@angular/router';
import { APPCONFIG } from './config';


// 3rd
import 'styles/material2-theme.scss';
import 'styles/bootstrap.scss';
// custom
import 'styles/layout.scss';
import 'styles/theme.scss';
import 'styles/ui.scss';
import 'styles/app.scss';
import { Global } from "../variables/Global";
import { EEValidators } from "../utils/eevalidators";

@Component({
  selector: 'app-root',
  template: require('./app.component.html'),
  providers: [LayoutService]
})
export class AppComponent implements OnInit {
  public AppConfig: any;
  constructor(private router: Router,
    private dataStg: DataStorageProvider) { }

  ngOnInit() {
    this.AppConfig = APPCONFIG;
    let _residenciaObj = this.dataStg.getValue('residenciaObj');
    if (_residenciaObj) {
      let residencia = JSON.parse(_residenciaObj as string);
      this.AppConfig.rol = this.dataStg.getValue('rol');
      this.AppConfig.user = this.dataStg.getValue('username');;
      APPCONFIG.brand.id = residencia.id;
      APPCONFIG.brand.nombre = residencia.nombre;
      APPCONFIG.brand.ic_logo = 'assets/images/ee-ger/ic_' + EEValidators.removeAccents(residencia.nombre) + '.png';
      APPCONFIG.brand.iw_logo = 'assets/images/ee-ger/iw_' + EEValidators.removeAccents(residencia.nombre) + '.png';
      APPCONFIG.userImage = 'assets/images/ee-ger/us_' + this.AppConfig.user + '.png';
    } else {
      this.router.navigate(["login"]);
    }

    // Scroll to top on route change
    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      document.body.scrollTop = 0;
    });
  }
}



