import { AccessGuard } from './../../providers/access-guard-provider';
import { RouterModule, Routes } from '@angular/router';

import { LayoutComponent } from './layout.component';
import { DashboardComponent } from "./../layout-pages/dashboard/dashboard.component";



const routes: Routes = [
  {
    path: 'app',
    component: LayoutComponent,
    children: [
      { path: '', redirectTo: '/app/dashboard', pathMatch: 'full' },
      { path: 'dashboard', component: DashboardComponent, data: { requiresLogin: true }, canActivate: [AccessGuard] },
      { path: '', loadChildren: './../layout-pages/layout-pages.module#LayoutPagesModule' },
    ]
  }
];

export const LayoutRoutingModule = RouterModule.forChild(routes);

