import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class CuotaProvider {

    constructor(private http: HttpClient) { }

    GetCuotaById(id) {
        return this.http.get(environment.url + 'cuota/' + id)
    }

    PostCuota(cuotaForm) {
        return this.http.post(environment.url + 'cuota', cuotaForm)
    }

    PutCuota(cuotaForm) {
        return this.http.put(environment.url + 'cuota', cuotaForm)
    }

    DeleteCuota(id) {
        return this.http.delete(environment.url + 'cuota/' + id)
    }
}


