import { FormControl } from '@angular/forms';


interface ValidationResult {
    [key: string]: boolean;
}
export class EEValidators {

    static dateValidator(control: FormControl): ValidationResult {
        var DATE_REGEXP = /(^(((0[1-9]|1[0-9]|2[0-8])(0[1-9]|1[012]))|((29|30|31)(0[13578]|1[02]))|((29|30)(0[4,6,9]|11)))(19|[2-9][0-9])\d\d$)|(^2902(19|[2-9][0-9])(00|04|08|12|16|20|24|28|32|36|40|44|48|52|56|60|64|68|72|76|80|84|88|92|96)$)/;
        if (control.value === null) return null;

        var valid = DATE_REGEXP.test(control.value.replace(/\D+/g, ''));
        if (!valid) {
            // control.setErrors({ format: 'invlid' });
            return { wrongDate: true };
        }
        return null;
    }

    static removeAccents(value) {
        return value .replace(/á/g, 'a')
                        .replace(/é/g, 'e')
                        .replace(/í/g, 'i')
                        .replace(/ó/g, 'o')
                        .replace(/ú/g, 'u')
  }

}


