import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { AuthenticationProvider } from './../../../providers/authentication.provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { Global } from './../../../variables/Global';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { APPCONFIG } from '../../config';

@Component({
  selector: 'my-app-header',
  styles: [],
  template: require('./header.component.html')
})

export class AppHeaderComponent implements OnInit {
  public AppConfig: any;
  private alertas;
  private alertasCount;
  constructor(private router: Router,
    private dataStg: DataStorageProvider,
    private systemProvider: AuthenticationProvider,
    private eeloading: EELoading
  ) { }

  ngOnInit() {
    this.AppConfig = APPCONFIG;
    console.log("header active");
    let rol = this.dataStg.getValue("rol")
    if (rol && rol === 'ADMIN') this.getNotifications(this.dataStg.getValue('residencia'));
  }

  getNotifications(residenciaID) {
    this.systemProvider
      .getNotifications(residenciaID)
      .subscribe(
      (data: any) => {
        this.eeloading.close();
        this.alertas = data;
        this.alertasCount = this.alertas.cobrosPendientes + this.alertas.chequesAVencer;
      },
      error => {
        this.eeloading.close();
      }
      )
  }

  logout() {
    this.dataStg.deleteAll();
    this.router.navigate(["login"]);
  }
}



