import { ErrorHandler } from './../../../handlers/errorHandler';
import { MatDialogRef } from '@angular/material';
import { ChequesProvider } from './../../../providers/cheques-provider';
import { APPCONFIG } from './../../../app/config';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { Component } from '@angular/core';

@Component({
    selector: 'ee-cheques-p-dialog',
    template: require('./ee-cheques-p-dialog.component.html')
})
export class EeChequesPDialogComponent {
    dataColumn;
    dataSource;
    constructor(
        public dialogRef: MatDialogRef<EeChequesPDialogComponent>,
        private chequesProvider: ChequesProvider,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private eeToast: EEToast) {
        this.chequesProvider.getChequesDisponibles(APPCONFIG.brand.id)
            .subscribe(
            (data : any) => {
                this.dataSource = [];
                data.forEach(element => {
                    let dataElement = {
                        id: element.chequeID,
                        fecha: element.fechaEmision.split('T')[0],
                        //fechaEmision: element.cheque.fechaEmision.split('T')[0],
                        fechaVencimiento: element.fechaVencimiento.split('T')[0],
                        monto: '$ ' + element.monto,
                        numero: element.numero,
                        banco: element.banco
                    }
                    this.dataSource.push(dataElement);
                });
                this.dataColumn = [{
                    key: 'fecha',
                    title: 'Fecha Emision'
                },
                {
                    key: 'fechaVencimiento',
                    title: 'Pago/Vto'
                },
                {
                    key: 'monto',
                    title: 'Monto'
                },
                {
                    key: 'numero',
                    title: 'Numero'
                },
                {
                    key: 'banco',
                    title: 'Banco'
                }]
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }


    retrievedOne(cheque) {
        this.dialogRef.close(cheque);
    }
}



