import { LoginModel } from './../models/loginModel';
import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class AuthenticationProvider {

  constructor(private http: HttpClient) { }


  registerUser(registerModel) {
    return this.http.post(environment.url + 'Account/Register', registerModel)
  }

  login(loginModel: LoginModel) {
    let body = "grant_type=password&userName=" + loginModel.username + "&password=" + loginModel.password;
    return this.http.post(environment.url + 'token', body)
  }

  getNotifications(residencia) {
    return this.http.get(environment.url + 'unidadnegocio/' + residencia + '/notifications')
  }
  loadResidencias(username) {
    return this.http.get(environment.url + 'residencia/username/' + username)
  }

}



