import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class FamiliarProvider {

    constructor(private http: HttpClient) { }

    GetFamiliarById(id) {
        return this.http.get(environment.url + 'familiar/' + id)
    }

    PostFamiliar(familiarForm) {
        return this.http.post(environment.url + 'familiar', familiarForm)
    }

    PutFamiliar(familiarForm) {
        return this.http.put(environment.url + 'familiar', familiarForm)
    }

    DeleteFamiliar(id) {
        return this.http.delete(environment.url + 'familiar/' + id)
    }
}


