import { FormGroup } from '@angular/forms';
import { Component, Input, Output } from '@angular/core';
import { EventEmitter } from "events";

export interface flxFormGroup {
    title?: String;
    formControls: Array<eeFormControl>;
    formGroup: Array<eeFormControl>;
}
export interface eeFormControl {
    type?: String;
    placeholder?: String;
    label?: String;
    required?: Boolean;
    disabled?: Boolean;
    minLength: Number;
    maxLength: Number;
    hide?: Boolean;
    blur?: Boolean;
}
@Component({
    selector: 'ee-form',
    template: require('./ee-form.component.html')
})
export class EeFormComponent {
    public formConstructor: Array<Object | eeFormControl | flxFormGroup> = [];
    public formGroupArray = {};
    @Input() public formContent: flxFormGroup;
    @Input() public formName: FormGroup;
    @Output() submitEmitter = new EventEmitter()
    ngOnInit() {
        /* Convert formContentObject to Array */
        for (let input in this.formContent) {
            this.formConstructor.push(input);
        }

        /*instance empty Objects*/
        this.formGroupArray = {};

        /*Search 'selects' for generate Arrays*/
        for (let inputForm in this.formContent) {
            let element = this.formContent[inputForm];
        }
    }
    onSubmit() {
        this.submitEmitter.emit('submitted');
        console.log(this.formName.valid);
    }
}



