import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { APPCONFIG } from './../../../app/config';
import { Component, Inject } from '@angular/core';
import { MatDialogRef } from "@angular/material";
import { MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';
import { EEValidators } from './../../../utils/eevalidators';

@Component({
    selector: 'ee-residencia-dialog',
    template: require('./ee-residencia-dialog.component.html'),
})
export class EeResidenciaDialogComponent {
    AppConfig;
    private residencias;
    constructor(private router: Router,
        public dialogRef: MatDialogRef<EeResidenciaDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any,
        private dataStg: DataStorageProvider) {
        this.AppConfig = APPCONFIG;
        this.residencias = data.residencias;
    }

    changeResidencia(value) {
        let residenciaObj = {
            nombre: value.nombre,
            id: value.residenciaID,
            ic_logo: 'assets/images/ee-ger/ic_' + EEValidators.removeAccents(value.nombre) + '.png',
            iw_logo: 'assets/images/ee-ger/iw_' + EEValidators.removeAccents(value.nombre) + '.png'
        }
        this.AppConfig.brand.nombre = residenciaObj.nombre;
        this.AppConfig.brand.id = residenciaObj.id;
        this.AppConfig.brand.ic_logo = residenciaObj.ic_logo;
        this.AppConfig.brand.iw_logo = residenciaObj.iw_logo;
        this.dataStg.setValue("residenciaObj", JSON.stringify(residenciaObj));
        this.router.navigate(["app/dashboard"]);
        this.dialogRef.close();
    }

}



