import { Component } from '@angular/core';
import { HotkeysService, Hotkey } from "angular2-hotkeys";
import { Router } from "@angular/router";

@Component({
  selector: 'my-app-layout',
  template: require('./layout.component.html'),
})
export class LayoutComponent {
  constructor(
    private _hotkeysService: HotkeysService,
    private router: Router
  ) {
    this._hotkeysService.add(new Hotkey('shift+h', (event: KeyboardEvent): boolean => {
      this.router.navigate(["app/dashboard"]);
      return false; // Prevent bubbling
    }));
    this._hotkeysService.add(new Hotkey('shift+1', (event: KeyboardEvent): boolean => {
      this.router.navigate(["app/gasto/1"]);
      return false; // Prevent bubbling
    }));
    this._hotkeysService.add(new Hotkey('shift+2', (event: KeyboardEvent): boolean => {
      this.router.navigate(["app/gasto/2"]);
      return false; // Prevent bubbling
    }));
    this._hotkeysService.add(new Hotkey('shift+3', (event: KeyboardEvent): boolean => {
      this.router.navigate(["app/gasto/3"]);
      return false; // Prevent bubbling
    }));
    this._hotkeysService.add(new Hotkey('shift+5', (event: KeyboardEvent): boolean => {
      this.router.navigate(["app/gasto/5"]);
      return false; // Prevent bubbling
    }));
    this._hotkeysService.add(new Hotkey('shift+7', (event: KeyboardEvent): boolean => {
      this.router.navigate(["app/gasto/7"]);
      return false; // Prevent bubbling
    }));
    this._hotkeysService.add(new Hotkey('shift+t', (event: KeyboardEvent): boolean => {
      this.router.navigate(["app/transferencias"]);
      return false; // Prevent bubbling
    }));
    this._hotkeysService.add(new Hotkey('shift+c', (event: KeyboardEvent): boolean => {
      this.router.navigate(["app/cobros/registrar"]);
      return false; // Prevent bubbling
    }));
  }
}



