import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class ResidenteProvider {

    constructor(private http: HttpClient) { }

    GetResidenteById(id) {
        return this.http.get(environment.url + 'residente/' + id)
    }

    GetResidentes(residencia) {
        return this.http.get(environment.url + 'residente/residencia/' + residencia)
    }

    GetResidentesAll(residencia) {
        return this.http.get(environment.url + 'residente/residenciaAll/' + residencia)
    }
    GetResidentesActivos(residencia) {
        return this.http.get(environment.url + 'residente/residencia/' + residencia + '/activo')
    }

    PostResidente(residenteForm) {
        return this.http.post(environment.url + 'residente', residenteForm)
    }

    PutResidente(residenteForm) {
        return this.http.put(environment.url + 'residente', residenteForm)
    }

    DeleteResidente(id) {
        return this.http.delete(environment.url + 'residente/' + id)
    }

    GetResidenteDetalle(id, residencia) {
        return this.http.get(environment.url + 'residente/detalle/' + id + '?residencia=' + residencia)
    }
}


