import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class ChequesProvider {

    constructor(private http: HttpClient) { }

    getChequesDisponibles(residencia) {
        return this.http.get(environment.url + 'cheques/disponibles?residencia=' + residencia)
    }

    confirmarCheque(chequeID, tipo) {
        return this.http.put(environment.url + 'cheques/' + chequeID + '/' + tipo, {})
    }
}



