import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class PersonalProvider {

    constructor(private http: HttpClient) { }

    GetPersonalById(id) {
        return this.http.get(environment.url + 'personal/' + id)
            .catch(this.handleError);
    }

    GetPersonales(residencia) {
        return this.http.get(environment.url + 'personal/residencia/' + residencia)
            .catch(this.handleError);
    }

    GetPersonalesAll(residencia) {
        return this.http.get(environment.url + 'personal/residenciaAll/' + residencia)
            .catch(this.handleError);
    }

    PostPersonal(personalForm) {
        return this.http.post(environment.url + 'personal', personalForm)
            .catch(this.handleError);
    }

    PutPersonal(personalForm) {
        return this.http.put(environment.url + 'personal', personalForm)
            .catch(this.handleError);
    }

    DeletePersonal(id) {
        return this.http.delete(environment.url + 'personal/' + id)
            .catch(this.handleError);
    }

    GetPersonalDetalle(id, residencia) {
        return this.http.get(environment.url + 'personal/detalle/' + id + '?residencia=' + residencia)
            .catch(this.handleError);
    }

    handleError(error: Response) {
        return Observable.throw("error");
    }
}


