import { MatSnackBar } from '@angular/material';
import { Injectable } from '@angular/core';

@Injectable()
export class EEToast {
    constructor(private snackBar: MatSnackBar) { }

    show(message, type) {
        this.snackBar.open(message, '', {
            duration: 4000,
            extraClasses: [type]
        });
    }
}


