import { EEToast } from './../providers/ee-toast-provider';
import { Injectable } from '@angular/core';
@Injectable()
export class ErrorHandler {
    private modal: any;
    constructor(
        private eeToast: EEToast
    ) { }

    handleError(error) {
        if (error.body && error.body.description) {
            this.eeToast.show(error.body.description, 'toast-error');
        } else {
            this.eeToast.show('Error con la llamada al servicio', 'toast-error');
        }
    }
}


