import { SubCategoriaProvider } from './../providers/subcategoria-provider';
import { AuthInterceptor } from './../providers/auth-interceptor-provider';
import { AppRoutingModule } from './app-routing.module';
import { LayoutModule } from './layout/layout.module';
import { EeFormComponent } from './../components/common/ee-form/ee-form.component';
