import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class PeriodoProvider {

    constructor(private http: HttpClient) { }

    GetPeriodoById(id) {
        return this.http.get(environment.url + 'periodo/' + id)
    }

    GetPeriodos(residencia) {
        return this.http.get(environment.url + 'periodo/residencia/' + residencia)
    }

    GetPeriodoDatos(periodo, residencia) {
        return this.http.get(environment.url + 'periodo/datos/' + periodo + '?residencia=' + residencia)
    }

    GetPeriodosDist(residencia) {
        return this.http.get(environment.url + 'periodo/dist/' + residencia)
    }

    PostPeriodo(periodoForm) {
        return this.http.post(environment.url + 'periodo', periodoForm)
    }

    PutPeriodo(periodoForm) {
        return this.http.put(environment.url + 'periodo', periodoForm)
    }

    DeletePeriodo(id) {
        return this.http.delete(environment.url + 'periodo/' + id)
    }

    GetCuotasByPeriodo(id, residencia) {
        return this.http.get(environment.url + 'periodo/' + id + '/cuotas?residencia=' + residencia)
    }
}


