import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'my-dashboard',
    template: require('./dashboard.component.html')
})
export class DashboardComponent {
    pageProps;
    user;
    constructor() {

    }
    getMonData = () => {
        const data = [];
        for (let i = 0; i < 13; i++) {
            data.push('Mon. ' + i);
        }
        return data;
    }

    monData = this.getMonData();

}



